import React from 'react';
import FrequentQuestions from '../components/FrequentQuestions/FrequentQuestions';
import files from '../images/undraw_secure_files_re_6vdh.svg';
import phone from '../images/undraw_emails_re_cqen.svg';
import partners from '../images/undraw_house_searching_re_stk8.svg';
import plan from '../images/undraw_business_plan_re_0v81.svg';
import marketing from '../images/undraw_social_dashboard_re_ocbd.svg';
import photos from '../images/undraw_moments_0y20.svg';
import security from '../images/undraw_security_on_re_e491.svg';
import together from '../images/undraw_business_deal_re_up4u.svg';
import accompany from '../images/undraw_team_spirit_re_yl1v.svg';
import planOfHouse from '../images/undraw_abstract_re_l9xy.svg';
import post from '../images/undraw_website_builder_re_ii6e.svg';
import answer from '../images/undraw_mobile_re_q4nk.svg';
import proffesional from '../images/undraw_certificate_re_yadi.svg';
import drone from '../images/undraw_follow_me_drone_kn76.svg';
import architect from '../images/undraw_designer_girl_re_h54c.svg';
import tour from '../images/undraw_video_files_fu10.svg';
import { Link } from 'react-router-dom';

const Services = () => {

  const scrollToTop = () => {
    window.scroll(0, 0);
  }

  return (
    <main className='services'>
        <div className='services--title'>
          <h1 className='services--title__text'>Servicios</h1>
        </div>
        <div className='services--sell'>
          <h2 className='services--sell__title'>Quers vender una propiedad?</h2>
          <h3 className='services--sell__subtitle'>Por qu vender con nosotros?</h3>
          <ul className='sell'>
            <div>
              <img src={plan} alt="Icono del servicio de tasacin"/>
              <h4>Tasacin</h4>
              <li>Realizamos una tasacin real y acordamos junto a vos una estrategia de precios para presentarnos de la mejor forma ante los compradores.
              </li>
            </div>
            <div>
              <img src={marketing} alt="Icono del servicio de marketing"/>
              <h4>Plan de marketing</h4>
              <li>Elaboramos un plan de marketing a medida para poder vender ms rpido y al mejor precio.
              </li>
            </div>
            <div>
              <img src={photos} alt="Icono del servicio de fotos"/>
              <h4>Fotografa profesional</h4>
              <li>Publicamos fotos profesionales de tu propiedad.
              </li>
            </div>
            <div>
              <img src={planOfHouse} alt="Icono del servicio del plano de la propiedad"/>
              <h4>Realizacin de planos</h4>
              <li>Realizamos un plano de tu propiedad para que los interesados puedan darse una idea de la distribucin antes de una visita presencial.
              </li>
            </div>
            <div>
              <img src={security} alt="Icono del servicio de protocolo de seguridad"/>
              <h4>Protocolo de seguridad</h4>
              <li>Contamos con un protocolo de seguridad previo a coordinar una visita a la propiedad.
              </li>
            </div>
            <div>
              <img src={post} alt="Icono del servicio de publicacin de la propiedad en diferentes sitios"/>
              <h4>Difusin</h4>
              <li>Publicamos tu propiedad en nuestra web, redes sociales y en los mejores portales del sector inmobiliario para lograr la mxima difusin posible.
              </li>
            </div>
            <div>
              <img src={together} alt="Icono del servicio de trabajo junto a otra inmobiliaria"/>
              <h4>Colaboracin</h4>
              <li>Trabajamos en colaboracin con cualquier inmobiliaria.
              </li>
            </div>
            <div>
              <img src={accompany} alt="Icono del servicio de acompaar al cliente"/>
              <h4>Asesoriamiento</h4>
              <li>Te acompaamos durante todo el proceso, aconsejndote con informacin fidedigna para que tomes la mejor decisin.
              </li>
            </div>
            <div>
              <img src={answer} alt="Icono del servicio de respuesta rpida"/>
              <h4>Rpida respuesta</h4>
              <li>Contamos con un rpido servicio de respuesta ante las consultas por la propiedad.
              </li>
            </div>
            <div>
              <img src={proffesional} alt="Icono del servicio de trabajar con profesionales"/>
              <h4>Profesionales</h4>
              <li>Estars acompaado por expertos certificados internacionalmente en el mercado residencial.
              </li>
            </div>
            <div>
              <img src={drone} alt="Icono del servicio de fotografa con drone"/>
              <h4>Filmaciones con drone</h4>
              <li>Realizamos filmaciones con drone y recorrido a pie de la propiedad.
              </li>
            </div>
            <div>
              <img src={tour} alt="Icono del servicio de tour 360"/>
              <h4>Tour 360</h4>
              <li>Realizamos tour 360 para efectivizar las vistas del inmueble.
              </li>
            </div>
          </ul>
        </div>
        <div className='services--buy'>
          <h2 className='services--buy__title'>Quers comprar una propiedad?</h2>
          <h3 className='services--buy__subtitle'>Por qu comprar una propiedad que ofrecemos?</h3>
          <ul className='buy'>
            <div>
              <img src={files} alt="Icono de control de documentacin"/>
              <h4>Control de documentacin</h4>
              <li>Controlamos que la documentacin est en condiciones para que la propiedad sea ofrecida a la venta.</li>
            </div>
            <div>
              <img src={architect} alt="Icono del servicio de realizacin de planos"/>
              <h4>Realizacin de planos</h4>
              <li>
                En cada propiedad encontrars un plano realizado por un arquitecto para que puedas darte una idea de su distribucin de la misma.
              </li>
            </div>
            <div>
              <img src={phone} alt="Icono de contacto"/>
              <h4>Rpida respuesta</h4>
              <li>
                Respondemos en periodo de tiempo muy corto cualquier consulta que nos realices por alguna propiedad.
              </li>
            </div>
            <div>
              <img src={partners} alt="Icono de representantes en tu bsqueda"/>
              <h4>Representacin</h4>
              <li>
              Si las propiedades no cumplen con tus expectativas de bsqueda, igual podemos ayudarte: comuncate con nosotros para que seamos tu representante en la bsqueda del inmueble que necesitas.
              </li>
            </div>
          </ul>
          <div className='services--button'>
            <Link to={'/brusi-propiedades/propiedades'} onClick={scrollToTop}>
              <button><span>Nuestras Propiedades</span></button>
            </Link>
          </div>
        </div>
        <FrequentQuestions />
    </main>
  )
}

export default Services;