import React from 'react';
import { Link } from 'react-router-dom';
import homeServiceImg from '../../images/Barrio Brickland-53.jpg';

const HomeServices = () => {

  const scrollToTop = () => {
    window.scroll(0, 0);
  }

  return (
    <div className='home--services'>
      <div>
        <img src={homeServiceImg} alt='Imagen de la seccin de servicios de la pgina de Inicio'/>
      </div>
      <div className='home--services__text'>
        <h2>Sabas que una buena fotografa de tu propiedad puede ayudar a que el inmueble se venda antes?</h2>
        <p>En <strong>Brusi Propiedades</strong> contamos con la tecnologa necesaria para facilitar la venta de tu inmueble.</p>
        <p>Realizamos fotos profesionales, videos a pie de toda la propiedad, tour virtual 360, fotos y videos con drone para vistas areas de la propiedad, planos digitales y ms para mostrar tu casa de la mejor manera.</p>
        <p>No dudes en visitar nuestra pgina de servicios para saber ms sobre ellos.</p>
        <Link to={'/brusi-propiedades/servicios'} onClick={scrollToTop}>
          <button><span>Nuestros Servicios</span></button>
        </Link> 
      </div>
    </div>
  )
}

export default HomeServices;